@charset "UTF-8";
@import '../functions';
$font-families: (
	Montserrat-Black:            '.otf',
	Montserrat-BlackItalic:      '.otf',
	Montserrat-Bold:             '.otf',
	Montserrat-BoldItalic:       '.otf',
	Montserrat-ExtraBold:        '.otf',
	Montserrat-ExtraBoldItalic:  '.otf',
	Montserrat-ExtraLight:       '.otf',
	Montserrat-ExtraLightItalic: '.otf',
	Montserrat-Italic:           '.otf',
	Montserrat-Light:            '.otf',
	Montserrat-LightItalic:      '.otf',
	Montserrat-Medium:           '.otf',
	Montserrat-MediumItalic:     '.otf',
	Montserrat-Regular:          '.otf',
	Montserrat-SemiBold:         '.otf',
	Montserrat-SemiBoldItalic:   '.otf',
	Montserrat-Thin:             '.otf',
	Montserrat-ThinItalic:       '.otf',
	MuseoSans-100:               '.otf',
	MuseoSans-300:               '.otf',
	MuseoSans-500:               '.otf',
	MuseoSans-700:               '.otf',
);

@each $family, $ext in $font-families {
	@font-face {
		font-family: $family;
		src: url('../fonts/#{$family}#{$ext}');
	}
}

$font-sizes: (11, 14, 16, 18, 20, 22, 24, 26, 27, 28, 29, 30, 31, 32, 33, 34, 36,37, 38, 39, 40, 42, 44, 48, 50,52, 56,58,60, 64, 65, 72, 90);

$font-proportions: (
	'110': 1.10,
	'120': 1.20,
	'130': 1.30,
	'140': 1.40,
	'150': 1.50,
	'160': 1.60,
);

@each $size in $font-sizes {
	.font-#{$size} {
		font-size: ptw($size);
	}

	.font-proportion {
		@each $proportion, $value in $font-proportions {
			&[data-proportion='#{$proportion}'] {
				.font-#{$size} {
					font-size: ptw($size) * $value;
				}
			}
		}
	}
}