#VideoIntro {
	position: absolute;
  	width: 100vw;
 	height: 100vh;
 	overflow: hidden;
 	background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
  	color: $white;
  	opacity: 1;
  	font-family: $font-regular;
	$opacity-transition: opacity 250ms ease-in-out 250ms;
	$url-images:   '../img/test/';
	transition: $opacity-transition;
	&::before{
		content: '';
		width: inherit;
		height: inherit;
		position: absolute;
		background-image: url(#{$url-images}back-degrad.png);
		background-repeat: no-repeat;
		background-size: contain;
		// background: linear-gradient(90deg, #1E1E28 15.42%, rgba(30, 30, 40, 0.0001) 100%), linear-gradient(180deg, rgba(30, 30, 40, 0.0001) 0%, #1E1E28 100%);;
	}



	#video-intro-skip-btn {
		position: absolute;
		width: ptw(418);
		height: pth(72);
		top: pth(928);
		text-align: center;
		left: ptw(1422);
		border-radius: pth(150);
		background: linear-gradient(90deg, rgba(25,138,255,1) 0%, rgba(55,194,234,1) 100%);
		font-size: pth(28);
		@include center-flex;
	
	}



	#text-top{
		position: absolute;
		width: ptw(325);
		height: pth(27);
		font-weight: 700;
		text-align: center;
		top: pth(893);
		left: ptw(1469);
		//border-radius: pth(150);
		font-size: pth(22);
		line-height: ptw(27);

	}

}

