  color: #64b5f6; }
  .blue-hover.active {
    color: #fafafa;
    width: 16.51vw;
    background-image: linear-gradient(to right, #1a76d2, #01bcd4);
    transition: transform 300ms ease;
    -webkit-animation: fadein 400ms;
    /* Safari, Chrome and Opera > 12.1 */
    animation: fadein 400ms; 
}

