#Error{
    $gray: #4b576f;
	width: 100vw;
	height: 100vh;
	position: absolute;
	top: 0;
	left: 0;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
	overflow: hidden;
	font-family: $font-regular;
    z-index: 18;

    .full-container{
        position: absolute;
        width: 100vw;
        transition: transform 180ms;
        height: 100vh;

        .logo{
             position: absolute;
             width: ptw(188);
             height: pth(72);
             left: pth(125);
             top:pth(93);
             background-repeat: no-repeat;
             background-size: contain;
             background-image: url("../img/logo.png");
        }
        .icono{
             position: absolute;
             width: ptw(724);
             height: pth(529);
             left: pth(1040);
             top:pth(299);
             background-repeat: no-repeat;
             background-size: contain;
             background-image: url("../img/credit_Card.png");
        }
    }  
    &._default{
        background-image: url("../img/bg_error.png");

        .title {
            position: absolute;
            width: 32.06vw;
            height: 7.70vw;
            display: -webkit-box;
            //text-align: center;
            top: ptw(300);
            left: ptw(272);
            color:#F5F5F5;
            font-size: ptw(36);
            //justify-content: center;
            //flex-direction: column
                -webkit-line-clamp: 4;
            /* align-items: center; */
            overflow: hidden;
            /* justify-content: center; */
            -webkit-box-orient: vertical;
            overflow-wrap: break-word;

        }

        .subtitle{
            display: none;
         }
        
         .codeLabel{
            position: absolute;
            width: ptw(374); //402
            height: ptw(219);
            top:ptw(430);
            left: ptw(272);
            color: #FAFAFA;
            text-align: center;
            line-height: ptw(146);
            font-size: ptw(180);
            font-family: $font-black;
            display: grid;
            align-items: center;
        }

        .labelMessage{
            position: absolute;
            box-sizing: border-box;
            top:ptw(433);
            left: ptw(155);
            color:#F5F5F5;
            text-align: center;
            width: ptw(308);
            height: ptw(86);
            font-size: ptw(22);
            justify-content: center;
            flex-direction: column
        }

        .suggestion {
            position: absolute;
            top: pth(749);
            left: ptw(272);
            width: ptw(520);
            height: pth(179);
            // @include center-flex;
            opacity: 0.8;
            font-family: $font-regular;
            align-items: flex-end;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: normal;
            text-align: left;
            // text-transform: uppercase;
            color:#F5F5F5;
            span {
                margin:0 ptw(5) 0 ptw(5);
            }
            .intructions-color{
                color: #59cbe8;
            }
            .intructions-bold{
                font-family: $font-bold;
            }
        }
    }
}