$img-path: "../img/";

#mainContainer{
	width: 100vw;
	height: 100vh;
}

#viewContainer{
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
}

@mixin center-flex{
	display:         flex;
	align-items:     center;
	justify-content: center;
}

.ui-component{
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	transition: opacity 190ms, transform 190ms;
	opacity: 1;

	&.hidden{
		opacity: 0;
		transform: translate3d(0,ptw(-20), 0);
	}
}

$w-square: 3.90625vw;
$h-square: 3.90625vw;

@function adjustValue($x){
	@return $x / 100 * 2;
}

@mixin positionToTranslate($x: 0, $y: 0, $z: 0){
	transform: translate3d($x, $y, $z);
}

@mixin adjustTranslate($x: 0, $y: 0, $z: 0){
	@include positionToTranslate(adjustValue($x) * $w-square, adjustValue($y) * $w-square, adjustValue($z) * $w-square);
}
@-webkit-keyframes rotate {
	0% {transform: rotate(0deg); }
	100% {transform: rotate(360deg)}
}
#Loading{
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	opacity: 0;
	max-width: 100vw;
	max-height: 100vh;
	overflow: hidden;
}
#Loading.shown{
	background-color: rgba(6,6,6,.8);
	z-index: 10;
	@include center-flex;
	opacity: 1;
	z-index: 20;
	.loading{
		position: relative;
		width: 7.8125vw;
		height: 7.8125vw;
		background-image: url("../img/loading/loading.svg");
		background-size: contain;
		-webkit-animation: rotate 2s linear infinite;
	}
	.loading-text{
		position: absolute;
		top: 61vh;
		width: 100vw;
		text-align: center;
		color: white;
		font-family: $font-regular;
		font-size: ptw(32);
		line-height: ptw(39);
	}
}

b, strong{
	font-family: $font-bold;
}

.white-container{
	position: absolute;
	width: 100vw;
	height: 100vh;
	top: 0;
	left: 0;
	z-index: 1;
	background-color: #f6f6f6;
	font-family: $font-regular;
	transition: transform 160ms linear, opacity 160ms linear;
	&.hidden{
		// opacity: 0;
		// transform: translate3d(0, ptw(-20), 0);
	}
	.container{
		position: absolute;
		width: ptw(792);
		height: ptw(832);
		top: ptw(120);
		left: ptw(564);
		background-color: #FDFDFD;
		box-shadow: 0 ptw(8) ptw(32) 0 rgba(0,0,0,0.1);
	}
}