#header-container {
	position: absolute;
	top: pth(64);
	left: ptw(22);
	height: pth(72);
	width: ptw(1820);
	display: flex;
	justify-content: space-between;

	$url-images:   '../img/test/';
	&.active {
		#back{
			transition: transform 180ms linear;
			background-image: url("../img/test/return_on.png");
		}
	}
	#back {
		width: ptw(168);
		height: pth(64);
		background: rgba(75, 87, 111, 0.25);
		margin-left: ptw(72);
		border-radius: pth(16);
		.icon-container {
			//width: ptw(64);
			//height: pth(64);
			margin-left: ptw(25);
			margin-top: ptw(17);
			//@include center-flex;
			&::before {
				content: '';
				position: absolute;
				height: pth(28);
				width: ptw(28);
				//background-image: url("../img/test/left_arrow.png");
				background-image: url("../img/test/icon_exit.png");
				background-repeat: no-repeat;
				background-size: contain;
			}
		}
		.txt{
			position: relative;
			left: 1.85vw;
			top: 0.15vw;
			font-size: ptw(24);
		}
		&.active {
			//.icon-container {
				background: linear-gradient(270deg, rgba(28,190,222,1) 0%, rgba(25,138,255,1) 100%);
			//}
			// background-image: url("../img/icons/return_on.png");
		}
	}
	#exit {
		width: ptw(104);
		height: pth(114);
		.icon-container {
			width: ptw(64);
			height: pth(64);
			border-radius: pth(32);
			// background: linear-gradient(270deg, rgba(28,190,222,1) 0%, rgba(25,138,255,1) 100%);
			@include center-flex;
			margin-right: ptw(20);
			margin-left: ptw(20);
			&::before {
				content: '';
				position: absolute;
				height: pth(34);
				width: ptw(34);
				//background-image: url("../img/test/exit-icon.png");
				background-image: url("../img/test/icon_exit.png");
				background-repeat: no-repeat;
				background-size: contain;
			}
		}
		&.active {
			.icon-container {
				background: linear-gradient(270deg, rgba(28,190,222,1) 0%, rgba(25,138,255,1) 100%);
			}
			// background-image: url("../img/icons/return_on.png");
		}
	}
}