#shopping-cart {
	position: absolute;
	top: pth(418);
	left: ptw(84);
	height: pth(133);
	width: ptw(1200);
	// background-color: salmon;
	$url-images:   '../img/test/';
	opacity: 1;
	display: flex;
	&.hidden {
		opacity: 0;
	}
	#botton-back{
		position: absolute;
		width: ptw(64);
		height: pth(136);
		background: rgba(75, 87, 111, 0.25);
		margin-left: ptw(32);
		border-radius: pth(16);
		transition: width 200ms linear;
		
			@include center-flex;
			&::before {
				content: '';
				position: absolute;
				height: pth(38);
				width: ptw(38);
				background-image: url("../img/test/left_arrow.png");
				background-repeat: no-repeat;
				background-size: contain;
			}
		&.active {
			//.icon-container {
				transition: width 200ms linear;
				//transition: transform 200ms ease-in;
				width: ptw(126);
				background: linear-gradient(270deg, rgba(28,190,222,1) 0%, rgba(25,138,255,1) 100%);
			//}
			// background-image: url("../img/icons/return_on.png");
			&::before {
				content: '';
				position: absolute;
				top:1.9vw;
			}

			.return{
				opacity: 1;
			}
		}

		.return{
			transition: width 200ms linear;
			position: absolute;
			top:ptw(80);
			width: ptw(81);
			height: ptw(22);
			opacity: 0;
			text-align: center;
			//left: ptw(27);
			font-weight: 400;
			font-size: ptw(18);
	        font-family: $font-regular;
			color: #F5F5F5;
		}
	}

	#bullet-list {
		width: ptw(16);
		height: pth(133);
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		margin-right: ptw(10);
		.bullet {
			width: ptw(16);
			height: pth(16);
			border: 1px solid #4B576F;
			//border: pth(1) solid rgba(75,87,111,1);
			box-sizing: border-box;
			border-radius: pth(30);
			&.active {
				height: pth(29);
				border-radius: pth(8);
				border: pth(1) solid rgba(239,108,0,1);
				background: linear-gradient(180deg, rgba(252,169,107,1) 0%, rgba(247,112,57,1) 100%);
			}
		}
	}
	#resume {
		width: ptw(865);
		display: flex;
		position: absolute;
		left: ptw(140);
		transition: left 200ms linear;
		.resumeItem {
			-webkit-animation: fade-in 1.2s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
			animation: fade-in 1.2s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
		}
		[data-type="show"] {
			width: ptw(300);
			height: pth(136);
			margin-right: ptw(24);
			box-sizing: border-box;
			//border:pth(1) solid rgba(75,87,111,1);

			border: 1px solid #4B576F;
  			border-radius: pth(17);
  			background-repeat: no-repeat;
			background-size: contain;
			background-position: center;
		}
		[data-type="ticket"] {
			position: relative;
			width: ptw(273);
			height: pth(136);
			margin-right: ptw(24);
			box-sizing: border-box;
