			#content {
				width: 13.98vw;
				// width: ptw(230);
				height: pth(81);
				display: flex;
				align-items: center;
				justify-content: space-around;
				#date {
					display: flex;
					width: ptw(138);
					height: pth(81);
					align-items: center;
					justify-content: center;
					flex-direction: column;
					[data-type="day"] {
						height: pth(42);
						font-size: pth(31);
						font-weight: bold;
						width: inherit;
