			.content {
				width: ptw(136);
				height: pth(106);
				display: flex;
				align-items: center;
				justify-content: space-between;
				flex-direction: column;
			}
			.title {
				height: pth(29);
				font-size: pth(24);
				line-height: pth(26);
			}
			.value {
				// font-family: $font-bold;
				height: pth(42);
				font-size: pth(34);
				line-height: pth(37);
				width: inherit;
				text-align: center;
				font-weight: bold;
				// white-space: nowrap;
				overflow: hidden;
	   			text-overflow: ellipsis;
	   			display: -webkit-box;
				-webkit-line-clamp: 1;
