.back-arrow {
	position: absolute;
	width: ptw(72);
	height: pth(72);
	top: pth(84);
	left: ptw(1700);
	background-image: url("../img/icons/salir_off.png");
	background-repeat: no-repeat;
	background-size: contain;
	transition: transform 180ms linear;
}
.back-arrow.active {
	transition: transform 180ms linear;
	background-image: url("../img/icons/salir_on.png");
	&:before {
		content: '';
		position: absolute;
		width: 5.75vw;
    	height: 6.67vh;
		left: -1.5vh;
		top: 7vh;
		background-repeat: no-repeat;
		background-image: url("../img/icons/tooltip.png");
	}
}
#OptionsReciveCode{
	position: absolute;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background-color: rgba(34, 36, 48, 0.95);;
	color: #F5F5F5;
	font-family: $font-light;
	overflow: hidden;
	z-index: 1;
	opacity: 0;
	transition: opacity 200ms;
	&.shown{
		opacity: 1;
	}
	&.off{
		opacity: 0;
	}
	.title {
        position: absolute;
        width: ptw(642);
        height: ptw(88);
        line-height: ptw(88);
        left: ptw(639);
        top: ptw(144);
        text-align: center;
        font-size: ptw(56);
        color: #F5F5F5;
        font-family: $font-light;
    }

	.titleRecover {
		left: ptw(306);
		top: pth(58);
		width: ptw(1309.5);
		height: pth(109.5);
		position: absolute;
		// font-family: $font-medium;
		letter-spacing: 0;
		line-height: pth(32);
		font-weight: 500;
		color: #fafafa;
	}

	.recognize-label{
		position: absolute;
		height: ptw(80);
		width: ptw(1112);
		top: ptw(856);
		color: #f5f5f5;
		font-size: ptw(25);
		line-height: ptw(32);
		left: ptw(404);
		text-align: center;
	}

	.selectRecover {
		left: ptw(404);
		top: ptw(210);
		width: ptw(1112);
		height: pth(80);
		position: absolute;
		// font-family: $font-medium;
		text-align: center;
		letter-spacing: 0;
		line-height: pth(80);
		color: #F5F5F5;
	}

	.bottom-disclaimer{
		position: absolute;
		bottom: ptw(80);
		left: ptw(404);
		color: #75787B;
		font-family: $font-light;
		font-size: ptw(22);
		line-height: ptw(26);
		width: ptw(1112);
