	}

	#recovery{
		left: ptw(705);
		bottom: pth(280);
		position: absolute;
		background: transparent;
		display: inline-flex;
		transition: transform 200ms ease-in-out;
		.itemRecover{
			 background-color: #444E63;
			padding: ptw(16);
			// box-shadow: 0 12px 24px 0 rgba(0,0,0,0.75);
			border-radius: 10.8px;
			width: ptw(464);
  			height: pth(408);
			margin-right: ptw(86);
			color: #F5F5F5;
			text-align: center;
			transition: transform 200ms ease 20ms;
			&.active {
				box-shadow: 0 0 0 0.5vw #198AFF;
				.button {
					color: $white;
					background-image: linear-gradient(90deg, #1A76D2 0%, #01BCD4 100%);
					border: none;
				}
				.icon_mail{
					&.phone-icon{
						background-image: url(../img/icons/phone_on.png), linear-gradient(90deg, #1A76D2 0%, #01BCD4 100%);
					}
					&.mail-icon{
						background-image: url(../img/icons/mail_on.png), linear-gradient(90deg, #1A76D2 0%, #01BCD4 100%);
					}
				}
			}
			.labelRecover {
				font-family:$font-light;
				line-height: pth(24);
				width: 100%;
				height: pth(24);
				margin-bottom: pth(28);
				position: relative;
				top: -1.9vw;
			}
			.methodRecover {
				font-family: $font-bold;
				// font-size: ptw(24);
				line-height: pth(34);
				width: ptw(463.89);
				height: pth(97);
				margin-bottom: pth(5);
				text-align: center;
			  	font-style: normal;
				line-height: normal;
				letter-spacing: normal;
			  	// text-overflow: ellipsis;
				//display: -webkit-box;
				overflow:hidden;
				-webkit-line-clamp: 2;
				-webkit-box-orient: vertical;
			  	align-items:     center;
				justify-content: center;
				overflow-wrap: break-word;
				top: -0.3vw;
			}

			.button {
				// background: $grey-300;
				font-size: ptw(29);
				font-family: $font-light;
				// font-size: ptw(16);
  				border-radius: 141.62px;
				color: $dark-grey-900;
				width: ptw(335);
				height: pth(65);
				letter-spacing: 0;
				border-radius: 5vw;
				line-height: pth(65);
				margin: 0 auto;
				position: relative;
				top: 0.26vw;
				color: #F5F5F5;
			}
			.icon_mail {
				background-repeat: no-repeat;
				background-size: cover	;
				background-position: center;
				position: relative;
				margin: 0 auto;
				width: pth(154);
  				height:pth(154);
