.back-arrow {
    position: absolute;
    width: ptw(72);
    height: pth(72);
    top: pth(84);
    left: ptw(1700);
    background-image: url("../img/icons/salir_off.png");
    background-repeat: no-repeat;
    background-size: contain;
    transition: transform 180ms linear;
}
.back-arrow.active {
    transition: transform 180ms linear;
    background-image: url("../img/icons/salir_on.png");
    &:before {
        content: '';
        position: absolute;
        width: 5.75vw;
        height: 6.67vh;
        left: -1.5vh;
        top: 7vh;
        background-repeat: no-repeat;
        background-image: url("../img/icons/tooltip.png");
    }
}
