.confirm-cvv-component-container{
	width: 100vw;
	height: 100vh;
	background-color: rgba(34, 36, 48, 0.95);
	color: #F5F5F5;
	font-family: $font-light;
	position: absolute;
	opacity: 0;
	transition: opacity 200ms;
	z-index: 10;
	&.shown{
		opacity: 1;
	}
	.cvv-title{
		position: absolute;
		top: ptw(283);
		left: ptw(1154);
		height: ptw(63);
		width: ptw(393);
		font-size: ptw(52);
	}
	.cvv-subtitle{
		position: absolute;
		top: ptw(388);
		left: ptw(1025);
		font-size: ptw(28);
		line-height: ptw(80);
		text-align: center;
		width: ptw(650);
		height: ptw(34);
	}
	.cvv-image{
		position: absolute;
		top: ptw(350);
		left: ptw(284);
		width: ptw(613);
		height: ptw(398);
		background-position: center;
		background-size: contain;
    	background-repeat: no-repeat;
		overflow: hidden;
	}
	.cvv-circule{
		position: absolute;
		top: ptw(413);
		left: ptw(716);
		width: ptw(177);
		height: ptw(144);
		background-image: url("../img/bancos/Path.png");
		background-position: center;
		background-size: contain;
    	background-repeat: no-repeat;
		overflow: hidden;
	}
	.cvv-container{
		position: absolute;
		width: ptw(342);
		height: ptw(130);
		left: ptw(1179);
		top: ptw(523);
		border: ptw(4) solid #198AFF;
		box-sizing: border-box;
		border-radius: ptw(24);
		box-shadow: 0 ptw(2) ptw(6) 0 rgba(0,0,0,0.3);
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0 0.4vw;
	}
	.cvv-digit{
		width: ptw(90);
		height: ptw(90);
		background-color: #E0E0E0;
		border-radius: ptw(12);
		text-align: center;
		color: #3B4559;
		font-size: 9vw;
		font-family: $font-regular;
		line-height: 8.5vw;
		margin-right: .67vw;
		&:last-child{
			margin: 0;
		}
		&.active{
			background-image: linear-gradient(270deg, #1CBEDE 0%, #198AFF 100%);
		}
	}
	.button{
		position: absolute;
		left: ptw(1193);
		width: ptw(313);
		box-sizing: border-box;
		border: ptw(1.5) solid rgba(26,118,210,0.5);
		height: ptw(64);
		line-height: ptw(58);
		text-align: center;
		font-size: ptw(28);
		color: #1A76D2;
		border-radius: ptw(150);
		bottom: 13vw;
		&.active{
			background-image: linear-gradient(270deg, #1CBEDE 0%, #198AFF 100%);
			color: #F6F6F6;
		}
	}
}