.nip-component-container{
	width: 100vw;
	height: 100vh;
	background-color: rgba(34, 36, 48, 0.95);
	color: #F5F5F5;
	font-family: $font-light;
	position: absolute;
	opacity: 0;
	transition: opacity 200ms;
	z-index: 4;
	&.shown{
		opacity: 1;
	}
	.nip-title{
		position: absolute;
		top: ptw(152);
		left: ptw(639);
		height: ptw(88);
		font-size: ptw(72);
	}
	.nip-subtitle{
		position: absolute;
		top: ptw(240);
		left: ptw(404);
		font-size: ptw(30);
		line-height: ptw(80);
		text-align: center;
		width: ptw(1112);
		height: ptw(80);
	}
	.nip-image{
		position: absolute;
		top: ptw(336);
		left: ptw(736);
		width: ptw(448);
		height: ptw(252);
		border-radius: ptw(24);
		background-image: url("../img/nip/myst.png");
		background-position: center;
		background-size: cover;
		overflow: hidden;
	}
	.nip-container{
		position: absolute;
		width: ptw(456);
		height: ptw(138);
		left: ptw(731);
		top: ptw(600);
		border: ptw(4) solid #198AFF;
		box-sizing: border-box;
		border-radius: ptw(24);
		box-shadow: 0 ptw(2) ptw(6) 0 rgba(0,0,0,0.3);
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0 0.4vw;
	}
	.nip-digit{
		width: ptw(90);
		height: ptw(90);
		background-color: #E0E0E0;
		border-radius: ptw(12);
		text-align: center;
		color: #3B4559;
		font-size: 9vw;
		font-family: $font-regular;
		line-height: 8.5vw;
		margin-right: .67vw;
		&:last-child{
			margin: 0;
		}
		&.active{
			background-image: linear-gradient(270deg, #1CBEDE 0%, #198AFF 100%);
		}
	}
	.sms-disclaimer, .terms-text{
		position: absolute;
		top: 40.58vw;
		left: 20.04vw;
		font-size: ptw(26);
		text-align: center;
		width: 62.92vw;
		line-height: ptw(30);
	}
	.resend-button{
		position: absolute;
		left: ptw(496);
		width: ptw(446);
		box-sizing: border-box;
		border: ptw(1.5) solid rgba(26,118,210,0.5);
		height: ptw(60);
		line-height: ptw(58);
		text-align: center;
		font-size: ptw(28);
		color: #1A76D2;
		border-radius: ptw(150);
		bottom: ptw(174);
		&.active{
			background-image: linear-gradient(270deg, #1CBEDE 0%, #198AFF 100%);
			color: #F6F6F6;
		}
		&.wrong-number{
			left: auto;
			right: ptw(496);
		}
	}
	.terms-text{
		top: 48.92vw;
		color: #BDC0C3;
		font-size: ptw(24);
		color: #BDC0C3;
		line-height: ptw(32);
	}
	.token-error-text{
		position: absolute;
		color: #FF5336;
		right: ptw(328);
		top: ptw(643);
		border: 2px solid #FF5336;
		width: ptw(380);
		font-size: ptw(28);
		border-radius: ptw(30);
		text-align: center;
		padding: .3vw 0;
		opacity: 0;
		&.shown{
			opacity: 1;
		}
		&:before {
			content: "";
			width: 0px;
			height: 0px;
			position: absolute;
			border-left: ptw(10) solid transparent;
			border-right: ptw(10) solid #FF5336;
			border-top: ptw(6) solid transparent;
			border-bottom: ptw(6) solid transparent;
			left: -1.02vw;
			top: 0.8vw;
		}

		&:after {
			content: "";
			width: 0px;
			height: 0px;
			position: absolute;
			border-left: ptw(10) solid transparent;
			border-right: ptw(10) solid rgba(34, 36, 48, 0.95);
			border-top: ptw(6) solid transparent;
			border-bottom: ptw(6) solid transparent;
			left: ptw(-16);
			top: 0.8vw;
		}
	}
}