#ExitApp{
	position: absolute;
  width: 100vw;
 	height: 100vh;
 	overflow: hidden;
  color: $white;
  opacity: 1;
  background-color: rgba(34,36,48,0.9);
  z-index: 5;
	$opacity-transition: opacity 250ms ease-in-out 250ms;
	transition: $opacity-transition;
  font-family: $font-regular;
  &.hidden{
		opacity: 0;
	}
  .title{
      height: pth(129);
      width: ptw(834);
      color: #F5F5F5;
      letter-spacing: 0;
      line-height: ptw(29);
      text-align: center;
      position: absolute;
      top: pth(415);
      left: ptw(543);
      font-size: ptw(36);
      justify-content: center;
      display: flex;
      align-items: center;
      text-align: center;



  }
  #container-opts{
      height: pth(86);
      width: ptw(850);
      position: absolute;
      top: pth(600);
      left: ptw(649.5);
     
    .item{
      float: left;
      height: pth(60);
      width: ptw(288);
      margin-right: ptw(20);
      font-size: ptw(27);
      border-radius: ptw(50);
      box-shadow: 0 0 0 ptw(1) #198AFF;
      @include center-flex;
      transition: transform 200ms ease-out;

      &.active{
        background: $blue-gradient;
      }
    }
  }
}