// Contiene funciones y clases que nos pueden ser de ayuda en cualquier parte del proyecto

// Centra elementos en un contenedor usando las propiedades de flex
@mixin center-flex{
	display:         flex;
	align-items:     center;
	justify-content: center;
}

@mixin flex-bottom-right {
	display: flex;
	align-items: flex-end;
	justify-content: flex-end;
}

@mixin flex-bottom-left {
	display: flex;
	align-items: flex-end;
	justify-content: flex-start;
}

@mixin left-center($w1, $w2){
	left: ($w1 / 2) - ($w2 / 2);
}

@mixin full-size{
	position: absolute;
	top:      0;
	bottom:   0;
	left:     0;
	right:    0;
}

@mixin triangle($direction, $color, $size) {
	width: 0;
	height: 0;
	@if $direction == 'up' {
		border-left: $size solid transparent;
		border-right: $size solid transparent;
		border-bottom: $size solid $color;
	}
	@else if $direction == 'down' {
		border-left: $size solid transparent;
		border-right: $size solid transparent;
		border-top: $size solid $color;
	}
}

@mixin tooltip($name, $left) {
	&:before {
		content: $name;
		position: absolute;
		width: pixels-to-vw(134);
		height: pixels-to-vh(32);
		background-color: $white;
		color: $black;
		left: $left;
		top: -7.5vh;
		opacity: 1;
		box-sizing: border-box;
		transform: translate3d(0, 0vh, 0);
		transition: transform 200ms cubic-bezier(0.4, 0, 1, 1) 500ms, opacity 200ms linear 500ms;
		@include center-flex;
	}
	&:after {
		content: '';
		position: relative;
		opacity: 1;
		transform: translate3d(0, 0vh, 0);
		transition: transform 200ms cubic-bezier(0.4, 0, 1, 1) 500ms, opacity 200ms linear 500ms;
		top: -1.8vh;
		left: 1.6vw;
		@include triangle('down', $white, 1vh);
	}
}