/**
 * Convierte unidades de pixeles a unidades vh|vw
 * @param  {int} $pixels
 * @param  {bool} $to:    false    Si es true convertira a vh
 * @return {float}                 Unidad en vh|vw
 */
@function pixels-to-v( $pixels, $to: false ) {
	$fixed:  1920; //regresar a full  hd
	$units: 1vw;
	@if($to) {
		$fixed: 1080; // regresar a full hd
		$units: 1vh;
	}
	$ret: ($pixels * 100) / $fixed;
	// @return $fixed;
	@return (to-fixed($ret, 2)) * $units; 
}


/* =============================================================== */
/* FUNCTIONS TO CALCULATE VW/VH */
/* _______________________________________________________________ */

@function pixels-to-vw( $pixels ) { @return pixels-to-v( $pixels ); }
@function pixels-to-vh( $pixels ) { @return pixels-to-v( $pixels , true ); }
@function ptw( $pixels ) { @return pixels-to-v( $pixels ); }
@function pth( $pixels ) { @return pixels-to-v( $pixels , true ); }

