.back-arrow {
	position: absolute;
	width: ptw(72);
	height: pth(72);
	top: pth(84);
	left: ptw(1700);
	background-image: url("../img/icons/salir_off.png");
	background-repeat: no-repeat;
	background-size: contain;
	transition: transform 180ms linear;
}
.back-arrow.active {
	transition: transform 180ms linear;
	background-image: url("../img/icons/salir_on.png");
	&:before {
		content: '';
		position: absolute;
		width: 5.75vw;
    	height: 6.67vh;
		left: -1.5vh;
		top: 7vh;
		background-repeat: no-repeat;
		background-image: url("../img/icons/tooltip.png");
	}
}
#mainContainer{
	clear: both;
}

$w-key:             ptw(98);
$h-key:             pth(98);
$keys-color:        #F5F5F5;
// $url-img:           '../img/icons/';
	
#NumericKeyboard.keyboard{
		width: 100vw;
		height: 100vh;
		top: pth(0);
		position: absolute;
		font-family: $font-light;
		font-weight: 200;
		z-index: 7;
		background-color: rgba(34, 36, 48, 1);
		transition: transform 400ms linear;
		overflow: hidden;
		// Animaci√≥n
		-webkit-animation: fadein 1000ms; /* Safari, Chrome and Opera > 12.1 */
		animation: fadein 1000ms;
		@keyframes fadein {
			from { opacity: 0; }
			to   { opacity: 1; }
		}

		/* Safari, Chrome and Opera > 12.1 */
		@-webkit-keyframes fadein {
			from { opacity: 0; }
			to   { opacity: 1; }
		}
		&:before {
			content: '';
			position: absolute;
			width: inherit;
			height: inherit;
			opacity: 0.9;
			text-align: center;
			@include center-flex;
		}
		
		&.off{
			opacity: 0;
		}
		
		.title{
			position: absolute;
			top: pth(150);
			left: ptw(404);
			height: pth(43);
			width: ptw(1112);
			color: #ffffff;
			font-weight: 500;
			text-align: center;
		}
		.subtitle{
			position: absolute;
			top: pth(210);
			left: ptw(403.5);
			height: pth(36);
			width: ptw(1113);
			color: #ffffff;
			font-weight: 300;
			text-align: center;
			line-height: 1.29;
		}
		.button{
			position: absolute;
			top: pth(864);
			left: ptw(790);
			height: pth(72);
			width: ptw(340);
			border-radius: 3px;
	  		border: solid 3px rgba(80, 81, 81, 0.5);
	 		background-color: rgba(43, 43, 43, 0.9);
		  	line-height: 1.14;
		  	text-align: center;
		  	color: #ffffff;
		  	@include center-flex;
		}
		.message {
			position: absolute;
			left: ptw(245);
			top: pth(-25);
			width: ptw(1359);
			height: pth(110);
			color: rgba(245, 245, 245, 1);;
			text-align: center;
			@include center-flex;
			align-items: flex-end;
			&.error{
				color: rgba(255, 83, 54, 1);
			}
		}
		
		.input-area {
			position: absolute;
			left: ptw(365);
			top: pth(100);
			width: ptw(1191);
			height: pth(290);
	  		border-radius: 2px;
			box-sizing: border-box;
			color: #F5F5F5;;
			@include center-flex;
			text-align: center;
			.input {
				color: #F5F5F5;	
				width: inherit;
				background-color: transparent;
				border: none;
				resize: none;
				outline: none;
				letter-spacing: 0;
				overflow: hidden;
				box-sizing: border-box;
				display: block;
				text-align: center;
				text-indent: ptw(20);
			}
		}
		.char-counter {
			position: absolute;
			left: ptw(1640);
			top: pth(203);
			width: ptw(50);
			height: pth(50);
			color:  rgba(2,165,210,1);
			@include center-flex;
			&.red{
				color: #e4002b;
			}
	
		}
		.containerInput {
			width: 100vw;
			height: pth(120);
			background: rgba(21, 21, 22, 0.9);
			position: relative;
			z-index: 0;
			.search-label{
				position: absolute;
				left: ptw(138.7);
				width: ptw(1030);
				height: pth(32);
				color: rgba(255,255,255,1);
				opacity: 0.5;
				letter-spacing: 0;
				display: flex;
				align-items: center;
			}
		}
		.qwerty-search{
			width: ptw(1444);
			height: pth(744);
			top: pth(504);
			left: ptw(773);
			float: left;
			position: absolute;
			border-radius: ptw(10);
			transition: opacity 200ms ease;
			//overflow: hidden;
			&.off{
				translateY: pth(300);
				opacity: 0;

			}
		}
		.row{
			height: $h-key;
			margin-bottom: pth(17);
			left: ptw(17);
			position: relative;
		}

		#KeyboardRow0{
			top: pth(0);
		}
		#KeyboardRow1{
			top: pth(20);
		}
		#KeyboardRow2{
			top: pth(30);
		}
		#KeyboardRow3{
			top: pth(45);
		}
		#KeyboardRow4{
			top: pth(-460);
			left: ptw(394);
			height: pth(448);
			width: ptw(215);
			color: #37C2EA;;
				span{
					margin-left: ptw(15);
				}
			.delete {
				width: ptw(215);
				height: pth(98);
				margin-bottom: pth(27);
				color: #37C2EA;
				//border: solid 3px #3B4559;;
				//background-color: #3B4559;;
			
				&.active {
				border: none;
				color: $white;
				}
			}
			.clean {
				width: ptw(215);
				height: pth(342);
				//border: solid 3px #e0e0e0;
				//background-color: #3B4559;
				color: #37C2EA;
				top: 1vh;
				&.active {
					border: none;
					color: $white;
				}
			}
		}
		#KeyboardRow5{
			top: pth(228);
			left: ptw(898);
			height: pth(76);
			width: ptw(350);
			.caps{
				height: pth(76);
				margin-bottom: pth(17);
				margin-right: ptw(27);
			}
			.special{
				height: pth(76);
				margin-bottom: pth(17);
			}
		}
		#KeyboardRow6{
			top: pth(228);
			left: ptw(607);
			height: pth(63);
			width: ptw(263);
			text-align: center;
			line-height: 2.5;
		
			.enter {
				color: $grey-50;
				// border: $grey-200 ptw(1) solid;
				height: pth(186);
				background-color: #3B4559;
				&.active {
					border: none;
				}
			}
		}
		.key{
			box-sizing: content-box;
			display: flex;
			position: relative;
			justify-content: center;
			align-items: center;
			float: left;
	  		height: $h-key;
			width: $w-key;
			color: rgba(245, 245, 245, 1);
		 	background-color: #3B4559;
			text-transform: uppercase;
			transition: background-image 0.125s ease-in 0s;
			-webkit-transition: background-image 0.125s ease-in 0s;
			&::after{
				content: attr(data-letter);
				text-transform: uppercase;
				// border : 1.7px  solid   rgba(89, 89, 89, .3);
			}
			&:last-child{
				margin-right: 0;
			}
			&.active {
				color:#F5F5F5;
				// border-radius: 2px;
	 			background: linear-gradient(270deg, #1CBEDE 0%, #198AFF 100%);
				// margin-right: 0;
				&.options{
					margin-left:0;
				}
			}
			&.options{
				width: 21.23vw;
				margin-left: 1vw;
			}
			&.clean{
				width: ptw(464);
				left: ptw(0);
				height: pth(76);
				text-transform: none;
				&.active{
					color:rgba(245, 245, 245, 1);
					border-radius: pth(16);
	 				background-color: #bb0a12;
	 				// width: ptw(298);
					height: pth(76);
				}
			}
			&.delete {
				width: ptw(457);
				text-transform: none;
				span{
					margin-left: ptw(15);
				}
				&::after {
					content: '';
					position: absolute;
					width: ptw(48);
					height: pth(48);
					left: ptw(10);
					top: pth(25);
					padding-right: ptw(5);
					background-position: left;
					background-repeat: no-repeat;
					background-size: contain;
					//background-image: $delete-off;
				}
				&.active {
					&::after {
						color: #ffffff;
					}
				}
			}
		}
	}