.purchase-container{
	position: absolute;
	top: 0;
	left: 0;
	overflow: hidden;
	width: 100vw;
	height: 100vh;
	background-image: url("../img/purchase/bg.png");
	color: #F5F5F5;
	font-family: $font-light;
	z-index: 10;
	opacity: 0;
	transition: opacity 200ms;
	&.shown{
		opacity: 1;
	}
	.title{
		position: absolute;
		right: ptw(134);
		top: ptw(190);
		width: ptw(960);
		height: ptw(120);
		line-height: ptw(120);
		font-size: ptw(100);
		text-align: center;
	}
	.subtitle{
		position: absolute;
		top: 17vw;
		right: ptw(184);
		width: ptw(860);
		height: ptw(152);
		line-height: 2.99vw;
		text-align: center;
		font-size: ptw(42);
	}
	.qr-image{
		position: absolute;
		top: ptw(472);
		right: ptw(457);
		width: ptw(296);
		height: ptw(296);
		background-color: #fff;
		background-size: contain;
	}
	.disclaimer{
		position: absolute;
		bottom: ptw(200);
		right: ptw(350);
		height: ptw(64);
		width: 26.56vw;
		text-align: center;
		font-family: $font-bold;
		font-size: ptw(24);
	}
	.disclaimer_1{
		position: absolute;
		bottom: 3.42vw;
		right: ptw(350);
		height: ptw(26);
		width: 44.31vw;
		text-align: center;
		font-family: $font-light;
		font-size: ptw(24);
		left: 84vh;
	}
	.back-label{
		position: absolute;
		bottom: ptw(130);
		right: ptw(158);
		width: ptw(912);
		height: ptw(32);
		line-height: ptw(32);
		font-size: ptw(22);
		text-align: center;
	}
	.phone{
		position: absolute;
		left: ptw(196);
		top: ptw(160);
		width: ptw(609);
		height: ptw(1200);
		background-image: url("../img/purchase/phone.png");
		background-size: contain;

		.image{
			position: relative;
			top: ptw(162);
			width: 19.7vw;
			box-sizing: border-box;
			border: ptw(2) solid white;
			background-size: 100%;
			background-repeat: no-repeat;
			border-radius: 1.6vw;
			height: 11.4vw;
			border: 0.4vw solid white;
			margin: 0 auto;
		}
	}
	.line{
		position: absolute;
		width: 22.3vw;
		height: 0.1vw;
		background-color: #DDD;
		top: 27.5vw;
		left: 4.5vw;
	}
	.event-label,
	.event-value,
	.name-label,
	.name-value,
	.date-label,
	.date-value,
	.time-label,
	.time-value,
	.tickets-label,
	.tickets-value,
	.zone-label,
	.zone-value,
	.celebration-label,
	.coupon-label,
	.celebration-value,
	.coupon-value{
		position: absolute;
		color: black;
		font-size: ptw(20);
	}

	.event-value,
	.name-value,
	.date-value,
	.time-value,
	.tickets-value,
	.zone-value,
	.celebration-value,
	.coupon-value{
		font-family: $font-regular;
	}

	.event-label,
	.event-value,
	.name-label,
	.name-value,
	.date-label,
	.date-value,
	.tickets-label,
	.tickets-value,
	.celebration-label,
	.celebration-value{
		left: ptw(95);
	}
	.time-label,
	.time-value,
	.zone-label,
	.zone-value,
	.coupon-label,
	.coupon-value{
		left: 18.5vw;
	}
	.event-label{
		top: ptw(400);
	}
	.event-value{
		top: ptw(440);
		font-family: $font-bold;
	}
	.name-label{
		top: ptw(510);
	}
	.name-value{
		top: ptw(550);
		font-family: $font-semibold;
	}
	.date-value {
		text-transform: capitalize;
	}
	.date-label, .time-label{
		top: ptw(610);
	}
	.date-value, .time-value{
		top: ptw(650);
	}
	.tickets-label,
	.zone-label{
		top: ptw(708);
	}
	
	.tickets-value,
	.zone-value{
		top: ptw(748);
	}
	.celebration-label,
	.coupon-label{
		top: ptw(806);
	}
	
	.celebration-value,
	.coupon-value{
		top: ptw(846);
	}
}