#mainContainer{
	clear: both;
}

$w-key:             ptw(75);
$h-key:             pth(75);
$keys-color:        rgba(255,255,255,1);
$url-img:           '../img/icons/';
// $space-bar-off:     url(#{$url-img}EspacioNGR.png);
// $space-bar-on:      url(#{$url-img}EspacioBCO.png);
$shift-off:         url(#{$url-img}ShiftBLUE.png);
$shift-on:          url(#{$url-img}ShiftBCO.png);
$shift-active:      url(#{$url-img}ShiftBLUE.png);
$shift-lock:        url(#{$url-img}ShiftHoldBLUE.png);
$shift-lock-active: url(#{$url-img}ShiftHoldWHITE.png);
// $special-off:       url(#{$url-img}EspecialesNGR.png);
// $special-on:        url(#{$url-img}EspecialesBCO.png);
$delete-off:        url(#{$url-img}rectangle-5.png);
$delete-on:         url(#{$url-img}DeleteBCO.png);
$keyboard-gradient : rgba(2,165,210,1);
// $icon-active-key: url(#{$url-img}over-teclado.png);
// $record:          url(#{$url-img}ShiftBTN.png);
// $history:         url(#{$url-img}Historial.png);


.keyboard{
	width: 100vw;
	height: 100vh;
	// opacity: 0.9;
	top: 0;
	position: absolute;
	font-family: Montserrat-Regular;
	font-weight: 200;
	z-index: 7;
	overflow: hidden;
	// background-image: linear-gradient(0deg, #185D81 2%, #092E56 98%);
	background-color: #eeeeee;
	// Animaci√≥n
	
	-webkit-animation: fadein 1000ms; /* Safari, Chrome and Opera > 12.1 */
	animation: fadein 1000ms;
	@keyframes fadein {
		from { opacity: 0; }
		to   { opacity: 1; }
	}

	/* Safari, Chrome and Opera > 12.1 */
	@-webkit-keyframes fadein {
		from { opacity: 0; }
		to   { opacity: 1; }
	}
	&:before {
		content: '';
		position: absolute;
		width: inherit;
		height: inherit;
		// background-image: $blim-gradient;
		opacity: 0.9;
	}
	&.off{
		opacity: 0;
	}
	.message {
		position: absolute;
		left: ptw(245);
		top: pth(60);
		width: ptw(1359);
		height: pth(110);
		color: #555f69;
		text-align: center;
		@include center-flex;
		// align-items: flex-end;
		// border-bottom: solid 2px #e0e0e0;
		&.error{
			color: #E4002B;
		}
	}
	.input-area {
		position: absolute;
		left: ptw(305);
		top: pth(179);
		width: ptw(1191);
		height: pth(290);
		border-bottom: pth(2) solid #4B576F;;
		border-top: pth(2) solid #4B576F;
		box-sizing: border-box;
		@include center-flex;
		.input {
		 	color: #F5F5F5;			
			width: inherit;
			background-color: transparent;
			border: none;
			resize: none;
			outline: none;
			letter-spacing: 0;
			overflow: hidden;
			box-sizing: border-box;
			display: block;
			text-align: center;
			font-family: Montserrat-Regular;
		}
	}
	.char-counter {
		position: absolute;
		left: ptw(1640);
		top: pth(302);
		width: ptw(50);
		height: pth(50);
		color: rgba(2,165,210,1);
		@include center-flex;
		&.red{
			color: #e4002b;
		}
	}
	.containerInput {
		width: 100vw;
		height: pth(120);
		background: rgba(21, 21, 22, 0.9);
		position: relative;
		z-index: 0;
		.search-label{
			position: absolute;
			left: ptw(138.7);
			width: ptw(1030);
			height: pth(32);
			color: rgba(255,255,255,1);
			opacity: 0.5;
			letter-spacing: 0;
			display: flex;
			align-items: center;
		}
	}
	.qwerty{
		width: ptw(1356);
		height: pth(448);
		top: pth(496);
		left:ptw(282);
		float: left;
		position: absolute;
		border-radius: ptw(10);
		transition: opacity 200ms ease;
		&.off{
			translateY: pth(300);
			opacity: 0;

		}
		&.uppercase{
			text-transform: uppercase;
		}
		&.special{
			.key{
				&::after{
					content: attr(data-special);
				}
			}
		}
		&.no-caps{
			.key{
				&::after{
					content: attr(data-letter);
					text-transform: lowercase;
				}
			}
			.special {
				&::after {
					content: "=/?";
				}
			}
		}
	}
	.row{
		height: $h-key;
		margin-bottom: pth(7);
		left: ptw(104);
		position: relative;
	}
	#recent-search{
			position: absolute;
			height: pth(59);
			// background: #5A5A5B;
			width: 100vw;
			.historyIcon{
				top: pth(20);
				left: ptw(192.7);
				height: pth(21);
				width: ptw(26);
				position: absolute;
				// background-image: $history;
				background-position: center;
				background-repeat: no-repeat;
			}
			#recent-list {
				position: absolute;
				left: ptw(235);
				width: ptw(800);
				display: flex;
				.search-keyword {
					width: ptw(193);
					height: pth(33);
					@include center-flex;
					color: $white;
				}
			}
			#recent-list{
				left: ptw(242.5);
				position: absolute;
				height: pth(59);
				width: (751.6);
				div{
					margin: (0 ptw(3.5));
					top: 1vh;
					display: flex;
					justify-content: center;
					align-items: center;
					height: pth(24);
					width: ptw(172);
					float: left;
					position: relative;
					font-size: ptw(18.67);
					color: #FFFFFF;
					letter-spacing: 0;
					text-overflow: ellipsis;
					overflow: hidden;
					white-space: nowrap;
					&.active{
							// width: ptw(200);
							// height: pth(26);
							// top: ptw(5);
							transform: scale3d(0.5,0.5,0.5);
							background-image: $keyboard-gradient;
							border-radius: ptw(66.67);
					}
				}
		}
	}
	#KeyboardRow0{
		top: pth(55);
		.record{
			background-repeat: no-repeat;
			background-position: center;
			position: relative;
			// background-image: $icon-active-key;
		}
	}
	#KeyboardRow1{
		top: pth(75);
	}
	#KeyboardRow2{
		top: pth(100);
	}
	#KeyboardRow3{
		top: pth(125);
	}
	#KeyboardRow4{
		top: pth(150);
	}
	#KeyboardRow5{
		top: pth(-355);
		left: ptw(0);
		height: pth(448);
		width: ptw(93);
		.caps{
			height: pth(281);
			margin-bottom: pth(27);
			border: solid 3px #e0e0e0;
			background-color: transparent;

			&.active {
				border: none;
				color: $white;
			}
		}
		.special {
			color: #1a76d2;
			height: pth(140);
			border: solid 3px #e0e0e0;
			background-color: transparent;
			
			&.active {
				border: none;
				color: $white;
			}
		}
	}
	#KeyboardRow6{
		top: pth(-810);
		left: ptw(1124);
		height: pth(448);
		width: ptw(215);
		color: #1a76d2;
		.delete {
			width: ptw(215);
			height: pth(75);
			margin-bottom: pth(27);
			color: #1a76d2;
			border: solid 3px #e0e0e0;
			background-color: transparent;
			text-align: right;
			vertical-align: middle;
			position: relative;
			span{
				margin-left: ptw(25);
			}
			&.active {
				border: none;
				color: $white;
			}
		}
		.enter {
			// color: $grey-50;
			// border: $grey-200 ptw(1) solid;
			width: ptw(215);
			height: pth(342);
			border: solid 3px #e0e0e0;
			background-color: transparent;
			color: #1a76d2;
			
			&.active {
				border: none;
				color: $white;
			}
		}
	}
	.key{
		margin-right: ptw(27);
		box-sizing: border-box;
		border-radius: pth(8);
		display: flex;
		position: relative;
		justify-content: center;
		align-items: center;
		float: left;
		height:$h-key;
		width: $w-key;
		// background-color: $grey-300;
		color: #555f69;
		background-color: #e0e0e0;
		transition: background-image 0.125s ease-in 0s;
		-webkit-transition: background-image 0.125s ease-in 0s;
		// &::after{
		// 	content: attr(data-letter);
		// 	font-size: ptw(22);
		// }
		&::after{
			content: attr(data-letter);
			text-transform: uppercase;
		}
		&:last-child{
			margin-right: 0;
		}
		&.active {
			background: $keyboard-gradient;
			color: $grey-200;
			transform: scale3d(1.05,1.05,1.05);
			box-shadow: 0 pth(12) pth(24) 0 rgba(0,0,0,0.50);
		}
		&.space-bar{
			position: relative;
			width: ptw(993);
			height: pth(38);
			background: $grey-300;
			border-radius: ptw(29);
			&::after {
				content: '';
				position: relative;
				width: inherit;
				height: inherit;
				background-position: center;
				background-repeat: no-repeat;
				// background-image: $space-bar-off;
			}
			&.active{
				background: $keyboard-gradient;
				// height: pth(26);
				&::after{
					// background-image: $space-bar-on;
				}
			}
		}
		&.caps {
			&::after {
				content: '';
				position: absolute;
				width: inherit;
				height: inherit;
				left: ptw(-4);
				background-position: center;
				background-repeat: no-repeat;
				background-image: $shift-off;
			}
			&.active {
				&::after {
					left: ptw(-1);
					background-image: $shift-on;
				}
			}
			&.shift {
				&::after {
					background-image: $shift-active;
				}
				&.active {
					&::after {
						background-image: $shift-on;
					}
				}
			}
			&.on {
				&::after {
					background-image: $shift-lock;
				}
				&.active {
					&::after {
						background-image: $shift-lock-active;
					}
				}
			}
		}
		&.special {
			&::after {
				content: '=/?';
				position: absolute;
				width: inherit;
				height: inherit;
				left: 0;
				background-position: center;
				background-repeat: no-repeat;
				@include center-flex;
			}
			&.active {
				&::after {
					color: $white;
					// background-image: $special-on;
				}
			}
			&.abc{
				&::after{
					content: 'ABC';
				}
			}
		}
		&.delete {
			&::after {
				content: '';
				position: absolute;
				width: ptw(42);
				height: pth(42);
				left: ptw(15);;
				top: pth(10);
				background-position: left;
				background-repeat: no-repeat;
				background-size: contain;
				background-image: $delete-off;
			}
			&.active {
				&::after {
					//background-image: $delete-on;
					top: pth(16);
					left: ptw(17);
					width: ptw(38);
					height: pth(38);
					color: $white;
				}
			}
		}
	}
}