#pages-container {
	position: relative;
	width: 100vw;
	height: pth(400);
	top: pth(680);
	overflow: hidden;
	// background-color: gray;
	-webkit-animation: slide-in-bottom 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	animation: slide-in-bottom 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
	#pages {
		box-sizing: border-box;
		.page {
			width: 100%;
			height: pth(400);
			display: flex;
			align-items: center;
			justify-content: center;
			flex-direction: column;
			transform: translate3d(0px, 0vh, 0px);
			transition: transform 0.7s linear, opacity 0.5s linear;
			color: white;
			opacity: 0;
			&.active {
				opacity: 1;
			}
		}
	}
}