.order-summary-container{
	position: absolute;
	width: 100vw;
	height: 100vh;
	color: #F5F5F5;
	overflow: hidden;
	opacity: 0;
	transition: opacity 200ms;
	background: linear-gradient(180deg, rgba(30,30,40,0) 0%, #1E1E28 100%), linear-gradient(90deg, #1E1E28 0%, rgba(30,30,40,0) 100%);
	font-family: $font-regular;
	background-image: url("../img/BG.png");;
	background-size: contain;
	background-repeat: no-repeat;
	&.shown{
		opacity: 1;
	}
	.main-title{
		position: absolute;
		top: ptw(200);
		width: 100vw;
		height: ptw(72);
		line-height: ptw(72);
		font-size: ptw(56);
		font-family: $font-black-italic;
		text-align: center;
	}
	.ticket_container{
		position: absolute;
		top: ptw(347);
		left: ptw(357);
		width: ptw(1350);
		height: ptw(176);
	}
	.choice-label{
		position: absolute;
		font-family: $font-italic;
		font-size: ptw(38);
		left: 10.54vw;
		bottom: ptw(368);
		&:before{
			position: absolute;
			content: '';
			width: ptw(72);
			height: ptw(72);
			background-image: url("../img/icons/icon-payment.png");;
			top: -.9vw;
    		left: -4vw;
    		background-size: contain;
    		background-repeat: no-repeat;
		}
	}
	.payments-list{
		position: absolute;
		left: ptw(120);
		bottom: ptw(88);
		width: 100000vw;
		height: ptw(240);
		transition: transform 180ms;
	}
	.payment-option{
		position: relative;
		float: left;
		width: ptw(464);
		color: #F5F5F5;
		height: 100%;
		border-radius: ptw(16);
		background-color: rgba(75,87,111,0.85);
		margin-right: ptw(24);
		&.active{
			background: linear-gradient(270deg, #1CBEDE 0%, #198AFF 100%);
		}
		.label{
			position: absolute;
			top: ptw(60);
			left: ptw(40);
			height: ptw(37);
			width: ptw(350);
			text-align: left;
			line-height: ptw(38);
			font-size: ptw(30);
			font-family: $font-light;
		}
		.payment-label{
			position: absolute;
			top: ptw(98);
			left: ptw(40);
			height: ptw(98);
			width: ptw(350);
			font-size: ptw(40);
			font-family: $font-regular;
			font-weight: bold;
			text-align: left;
			line-height: ptw(32);
			bottom: ptw(31);
			line-height: ptw(49);

		}
		.img{
			position: absolute;
			top: ptw(92);
			left: ptw(352);
			height: ptw(72);
			width: ptw(72);
			background-repeat: no-repeat;
			background-size: contain;
			background-position: center;
		}
		.bage{
			position: absolute;
			top: ptw(-20);
			left: ptw(168);
			height: ptw(51);
			width: ptw(320);
			background-color: #FBC014;
			border-radius: ptw(36);
			font-family: $font-regular;
			font-weight: bold;
			text-align: center;
			font-size: pth(27);
			color:  #3B4559;;
			//display: inline-grid;
			align-items: center;
			display: flex;
			justify-content: center;
		}
	}
	#botton-back{
		position: absolute;
		width: ptw(90);
		height: pth(192);
		background: rgba(75, 87, 111, 0.25);
		//margin-left: ptw(32);
		border-radius: pth(16);
		transition: width 200ms linear;
		top: 32.2vh;
    	left: 12.67vw;

			@include center-flex;
			&::before {
				content: '';
				position: absolute;
				height: pth(38);
				width: ptw(38);
				background-image: url("../img/test/left_arrow.png");
				background-repeat: no-repeat;
				background-size: contain;
			}
		&.active {
			//.icon-container {
				transition: width 200ms linear;
				//transition: transform 200ms ease-in;
				width: ptw(136);
				background: linear-gradient(270deg, rgba(28,190,222,1) 0%, rgba(25,138,255,1) 100%);
			//}
			// background-image: url("../img/icons/return_on.png");
			&::before {
				content: '';
				position: absolute;
				top:2.9vw;
			}

			.return{
				opacity: 1;
			}
		}

		.return{
			transition: left 600ms linear;
			position: absolute;
			top: 6.17vw;
			width: ptw(81);
			height: ptw(22);
			opacity: 0;
			left: ptw(27);
			font-weight: 400;
			font-size: ptw(18);
	        font-family: $font-regular;
			color: #F5F5F5;
		}
	}
	.ticket_container {
		width: ptw(1260);
		display: flex;
		justify-content: center;
