		[data-type="show"] {
			width: ptw(432);
			height: pth(192);
			margin-right: ptw(24);
			box-sizing: border-box;
			border:pth(2) solid rgba(75,87,111,1);
  			border-radius: pth(17);
  			background-repeat: no-repeat;
			background-size: cover;
			background-position: center;
		}
		[data-type="ticket"] {
