					width: ptw(213);
					height: pth(81);
					align-items: center;
					justify-content: center;
					flex-direction: column;
					[data-type="day"] {
						height: pth(42);
						font-size: pth(48);
						font-weight: bold;
						width: inherit;
						text-transform: capitalize;
						text-align: center;
						display: flex;
						justify-content: center;
					}
					[data-type="month"] {
						height: pth(26);
						font-size: pth(32);
						@include center-flex;
						color: rgba(245,245,245,1);
						text-transform: capitalize;
